/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #000000;
  font-size: 2rem;
}

.container {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.section {
  padding-top: 2rem;
  padding-bottom: 2rem;
}

.center-content {
  max-width: 800px;
}

.align-right {
  display: flex;
  justify-content: flex-end;
}

.w-100 {
  width: 100%;
}

.btn-yellow-inline {
  background-color: #E08709;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2rem;
  display: inline-block;
  border: none;
}
.btn-yellow-inline:hover {
  background-color: #b96f07;
  cursor: pointer;
}

.btn-yellow-block {
  background-color: #E08709;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2rem;
  display: block;
  border: none;
}
.btn-yellow-block:hover {
  background-color: #b96f07;
  cursor: pointer;
}

.btn-green {
  background-color: #71B100;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2rem;
  display: inline-block;
  border: none;
}
.btn-green:hover {
  background-color: #578800;
  cursor: pointer;
}

.btn-green-block {
  background-color: #71B100;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2rem;
  display: block;
  border: none;
}
.btn-green-block:hover {
  background-color: #578800;
  cursor: pointer;
}

.btn-red-block {
  background-color: rgb(189, 7, 7);
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2rem;
  display: block;
  border: none;
  width: 100%;
}
.btn-red-block:hover {
  background-color: #960606;
  cursor: pointer;
}

.darkMode {
  background-color: #1a1a1a;
}
.darkMode p {
  color: #FFFFFF;
}
.darkMode a, .darkMode h1, .darkMode h2, .darkMode h3, .darkMode h4 {
  color: #e1e1e1;
}
.darkMode .announcement {
  background-color: #333333;
  border: 1px solid #333333;
}
.darkMode .announcement .price {
  color: #71B100;
}
.darkMode .house-resume .price {
  color: #71B100;
}
.darkMode .icon-house {
  filter: invert(100%);
}
.darkMode .about-text blockquote {
  color: #FFFFFF;
}
.darkMode .form label,
.darkMode .form legend,
.darkMode .form p {
  color: #e1e1e1;
}
.darkMode .form input:not([type=submit]),
.darkMode .form textarea,
.darkMode .form select {
  background-color: #333333;
  color: #FFFFFF;
  border: 1px solid #333333;
}
.darkMode .properties-list thead th {
  color: #FFFFFF;
}
.darkMode .properties-list tbody td {
  color: #FFFFFF;
}

/** UI **/
.no-webp .start {
  background-image: url(../img/header.jpg);
}

.webp .start {
  background-image: url(../img/header.webp);
}

.header {
  background-color: #333333;
  padding: 1rem 0 3rem 0;
}
.header.start {
  background-position: center center;
  background-size: cover;
}
@media (min-width: 768px) {
  .header.start {
    min-height: 55rem;
  }
}
@media (min-width: 768px) {
  .header.start .header-content {
    min-height: 55rem;
  }
}

.header-content {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.header-content h1 {
  text-align: left;
  color: #FFFFFF;
  font-weight: 700;
  line-height: 1.5;
}

.superior {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
@media (min-width: 768px) {
  .superior .mobile-menu {
    display: none;
  }
}
.superior .mobile-menu img {
  width: 3rem;
}
.superior .mobile-menu:hover {
  cursor: pointer;
}
.superior .theme-icon img {
  width: 3rem;
  margin-left: 3rem;
  filter: invert(100%);
}
.superior .theme-icon:hover {
  cursor: pointer;
}

.footer {
  background-color: #333333;
}
.footer .copyright {
  color: #FFFFFF;
  text-align: center;
}
.footer .nav {
  visibility: visible;
  opacity: 1;
}
.footer p {
  margin: 0;
}

.bar {
  display: flex;
  flex-direction: column;
  padding-top: 3rem;
}
@media (min-width: 768px) {
  .bar {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
}

.nav {
  visibility: hidden;
  opacity: 0;
  transition: visibility 0.3s ease-in-out, opacity 0.5s linear;
}
@media (min-width: 768px) {
  .nav {
    display: flex;
    opacity: 1;
    visibility: visible;
  }
}
.nav a {
  color: #FFFFFF;
  font-size: 2rem;
  display: block;
  text-align: center;
}
@media (min-width: 768px) {
  .nav a {
    margin-right: 2rem;
  }
  .nav a:last-of-type {
    margin-right: 0;
  }
}

.view-nav {
  visibility: visible;
  opacity: 1;
}

@media (min-width: 768px) {
  .about-us-icons {
    display: grid;
    gap: 3rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.icon {
  text-align: center;
}
.icon img {
  height: 10rem;
}
.icon h3 {
  text-transform: uppercase;
}

@media (min-width: 768px) {
  .announcements-container {
    display: grid;
    gap: 3rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.announcement {
  background-color: #e1e1e1;
  border: 1px solid #b3b3b3;
  margin-bottom: 3rem;
}
@media (min-width: 768px) {
  .announcement {
    margin-bottom: 0;
  }
}
.announcement:last-of-type {
  margin-bottom: 0;
}
.announcement .announcement-content {
  padding: 2rem;
}
.announcement h3, .announcement p {
  margin: 0;
}

.price {
  color: #71B100;
  font-weight: 700;
  font-size: 3rem;
}

.characteristics-icons {
  display: flex;
  justify-content: space-between;
  list-style: none;
  padding: 0;
  max-width: 40rem;
}
.characteristics-icons li {
  flex: 1;
  display: flex;
}
.characteristics-icons li img {
  flex: 0 0 3rem;
  margin-right: 1rem;
}

.no-webp .contact-image {
  background-image: url("../img/encuentra.jpg");
}

.webp .contact-image {
  background-image: url("../img/encuentra.webp");
}

.contact-image {
  background-position: center center;
  background-size: cover;
  padding: 2.5rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.contact-image h2 {
  color: #FFFFFF;
  margin: 0;
  font-weight: 900;
}
.contact-image p {
  color: #FFFFFF;
}

@media (min-width: 768px) {
  .section-inferior {
    display: grid;
    grid-template-columns: 2fr 1fr;
    column-gap: 3rem;
  }
}
.input-blog {
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .input-blog {
    display: grid;
    grid-template-columns: 1fr 2fr;
    column-gap: 3rem;
  }
}
.input-blog:last-of-type {
  margin-bottom: 0;
}

.input-date span {
  color: #E08709;
}

.input-text a {
  color: #000000;
}
.input-text h4 {
  margin: 0;
  line-height: 1.4;
}
.input-text h4::after {
  content: "";
  display: block;
  width: 15rem;
  height: 0.5rem;
  background-color: #71B100;
}

.testimonial {
  background-color: #71B100;
  font-size: 2.4rem;
  padding: 2rem;
  color: #FFFFFF;
  border-radius: 2rem;
}
.testimonial blockquote {
  padding-left: 4rem;
  position: relative;
}
.testimonial blockquote::before {
  content: "";
  background-image: url(../img/comilla.svg);
  background-repeat: no-repeat;
  width: 4rem;
  height: 6rem;
  display: block;
  position: absolute;
  left: -1rem;
}
.testimonial p {
  text-align: right;
  color: #FFFFFF;
}

@media (min-width: 768px) {
  .about-us {
    display: grid;
    gap: 3rem;
    grid-template-columns: repeat(2, 1fr);
  }
}

.about-text blockquote {
  font-weight: 900;
  font-size: 2.2rem;
  margin: 3rem 0 0 0;
  padding-left: 1rem 0 3rem 0;
}

.form {
  margin-top: 3rem;
}
.form p {
  font-size: 1.4rem;
  color: #333333;
  margin: 1rem 0 0 0;
}
.form legend {
  font-size: 2rem;
  color: #333333;
}
.form label {
  font-weight: 700;
  text-transform: uppercase;
  display: block;
}
.form input:not([type=submit]),
.form textarea,
.form select {
  padding: 1rem;
  display: block;
  width: 100%;
  background-color: #FFFFFF;
  border: 1px solid #e1e1e1;
  border-radius: 1rem;
  margin-bottom: 2rem;
}
.form textarea {
  height: 20rem;
  resize: none;
}
.form input[type=radio] {
  margin: 0;
  width: auto;
}
.form .form-contact {
  max-width: 40rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

/** Admin **/
.alert {
  padding: 1rem;
  text-align: center;
  color: #FFFFFF;
  font-weight: 700;
  margin: 1rem 0;
}
.alert.error {
  background-color: rgb(189, 7, 7);
}
.alert.success {
  background-color: #71B100;
}

.properties-list {
  margin-top: 4rem;
  width: 100%;
  border-spacing: 0;
}
.properties-list thead {
  background-color: #71B100;
}
.properties-list thead th {
  color: #FFFFFF;
  padding: 2rem;
}
.properties-list tbody {
  text-align: center;
}
.properties-list .table-image {
  width: 10rem;
}

.image-update {
  width: 20rem;
  margin-left: 2rem;
  margin-bottom: 2rem;
}