.no-webp .contact-image{
    background-image: url('../img/encuentra.jpg');
}

.webp .contact-image{
    background-image: url('../img/encuentra.webp');
}

.contact-image{
    background-position: center center;
    background-size: cover;
    padding: 2.5rem 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    h2{
        color: $white;
        margin: 0;
        font-weight: $fontBlack;
    }
    p{
        color: $white;
    }
}